@font-face {
  font-family: "PixelDigivolveCyrillic";
  src: local("PixelDigivolveCyrillic"), local("PixelDigivolveCyrillic"), url("../fonts/pixelcyr/PixelDigivolveCyrillic.woff2") format("woff2"), url("../fonts/pixelcyr/PixelDigivolveCyrillic.woff") format("woff"), url("../fonts/pixelcyr/PixelDigivolveCyrillic.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #8353E3;
  color: #fff;
  font-family: Mulish, Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.container {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

.wrapper {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
}

.radio {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.radio__field {
  display: inline-block;
  margin-right: 10px;
  width: 22px;
  height: 22px;
  background: url("../images/icons/radio.svg") no-repeat center/contain;
  position: relative;
}
.radio__field::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0.6);
  width: 6px;
  height: 6px;
  background: #fff;
  opacity: 0;
  transition: transform 0.2s ease;
}
.radio__input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.radio__input:checked ~ .radio__field::after {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

.title {
  font-family: PixelDigivolveCyrillic;
  text-align: center;
  font-size: 22px;
  line-height: 150%;
  font-weight: 400;
}

.btn {
  min-width: 183px;
  min-height: 43px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #000;
  text-transform: uppercase;
  font-family: PixelDigivolveCyrillic;
  font-weight: 400;
  background: url("../images/icons/button.png") no-repeat center/100% 100%;
  position: relative;
}
.btn::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  left: 0;
  background: url("../images/icons/button-hover.png") no-repeat center/100% 100%;
}
.btn span {
  position: relative;
  z-index: 5;
}
.btn:hover::after, .btn:focus::after {
  z-index: 1;
}
.btn--yellow {
  background: url("../images/icons/button-yellow.png") no-repeat center/100% 100%;
}

