  margin-bottom: 26px;
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.terms__heart {
  position: absolute;
}
.terms__heart-1 {
  left: -10px;
  top: 120px;
}
.terms__heart-2 {
  right: 20px;
  top: 0;
}
.terms__heart-3 {
  right: 23px;
  bottom: -15px;
}
.terms__heart-4 {
  left: 0px;
  bottom: 40px;
}
.terms__heart-5 {
  right: -10px;
  bottom: 125px;
}
.terms__heart-6 {
  right: 10%;
  bottom: 20px;
}
.terms__heart-red {
  position: absolute;
  bottom: 10px;
  left: 30%;
}
.terms__logo {
  width: 245px;
  margin-bottom: 40px;
  display: inline-block;
