  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  cursor: pointer;
}
.terms__label:last-child {
  margin-bottom: 0;
}
.terms__label-checkbox {
  width: 30px;
  min-width: 30px;
  height: 30px;
  margin-top: 5px;
  margin-right: 28px;
  background: url("../images/icons/check-empty.svg") no-repeat center/contain;
  position: relative;
}
.terms__label-checkbox::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: url("../images/icons/check.svg") no-repeat center/contain;
  opacity: 0;
}
.terms__label input {
  display: none;
}
.terms__label input:checked ~ .terms__label-checkbox {
  background: none;
}
.terms__label input:checked ~ .terms__label-checkbox::after {
  opacity: 1;
}
.terms__broken {
  width: 85px;
  margin-bottom: 26px;
}
.terms__text {
  margin-top: 43px;
  margin-bottom: 36px;
