}
.terms__btns {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.terms__btns .btn {
  margin-bottom: 40px;
}
.terms--match .terms__header {
  flex-direction: column-reverse;
}
.terms--match .terms__logo {
  margin-bottom: 0;
}
.terms--match .terms__heart-1 {
  top: 90px;
}
.terms--match .terms__heart-3 {
  right: -15px;
}

